<template>
  <div>
    <div class="skipLink">
      <a href="#mainContent">Skip to Main Content</a>
    </div>
    <b-nav class="navbar navbar-expand-sm navbar-light border-bottom justify-content-between"
    >
      <a class="navbar-brand" href="/">eggApp</a>
      <div class="navbar-nav" @click="onclickMenu">
        <b-nav-item class="nav-item nav-link active" to="/Spot"><font-awesome-icon icon="shopping-cart" />&nbsp;買い物スポット</b-nav-item>
        <b-nav-item class="nav-item nav-link active" to="/List"><font-awesome-icon icon="images" />&nbsp;おすすめ商品</b-nav-item>
        <!-- <b-nav-item class="nav-item nav-link active" to="/Grid">Grid</b-nav-item>
        <b-nav-item class="nav-item nav-link active" to="/List">List</b-nav-item>
        <b-nav-item class="nav-item nav-link active" to="/Master_Detail">Master_Detail</b-nav-item> -->
      </div>
    </b-nav>
  </div>
</template>

<script>
import moment from 'moment';

moment.locale('ja');

import CONSTANTS from "@/constants";

export default {
  name: "TheNavBar",
  serverCacheKey: false,

  data() {
    return {
      fromnow: null,
      toend: null,
      checked: false,
      selected: null,
      disabledemo: true,
      options: [
        { value: null, text: "From now to the future below minutes" },
        { value: 1, text: "the future 1 minutes" },
        { value: 5, text: "the future 5 minutes" },
        { value: 10, text: "the future 10 minutes" },
        { value: 15, text: "the future 15 minutes" },
      ],

      animationId: 0,
      hours: 0,
      minutes: 0,
      second: 0,
      milisecond: 0,
      startTime: 0,// スタート時間
      endTime: 0,// ストップ押した時間
      diffTime: 0, //スタートとストップ押した時の差分
      flag: false,
    }
  },
  beforeDestroy() {
    clearInterval(this.animationId);
    this.stopTimer();
  },
  filters:{
  	zeroPadding(value){	
    	return value.toString().padStart(2, 0);
    },
    showMiliseconds(value){
      return value.toString().padStart(3, 0);
    }
  },
  created() {
    if(!this.$eventHub._events['changed-menu-notify-navbar']) {
      // イベント通知のハンドラを登録
      this.$eventHub.$on('changed-menu-notify-navbar', this.onChangedMenuNotify);
    }
  },
  mounted() {
    if(this.apptype.apptype == CONSTANTS.APPTYPE.RFTAG) {

    } else if(this.apptype.apptype == CONSTANTS.APPTYPE.CAEMRA) {

    }
  },
  methods: {
    onclickMenu: function(evt) {
      console.log(evt.target.text.trim());
      if( evt.target.text.trim() === CONSTANTS.APPTYPE.RFTAG_STR) {
        this.$store.dispatch('doUpdateApptype', CONSTANTS.APPTYPE.RFTAG);

        this.$router.go({path: '/'+CONSTANTS.APPTYPE.RFTAG_STR, force: true});

        // 切り替え通知
        this.$eventHub.$emit('changed-menu-notify-camera', evt.target.text.trim());

      } else if( evt.target.text.trim() === CONSTANTS.APPTYPE.CAMERA_STR) {
        this.$store.dispatch('doUpdateApptype', CONSTANTS.APPTYPE.CAMERA);

        this.$router.go({path: '/'+CONSTANTS.APPTYPE.CAMERA_STR, force: true});
        // 切り替え通知
        this.$eventHub.$emit('changed-menu-notify-rftag', evt.target.text.trim());
        
      } else {
        console.log("None Application Type", evt.target.text.trim());
        
        this.$eventHub.$emit('changed-menu-notify-camera', evt.target.text.trim());
        this.$eventHub.$emit('changed-menu-notify-rftag', evt.target.text.trim());
      }

      this.$eventHub.$emit('changed-menu-notify-navbar', evt.target.text.trim());
    },
    onChangedMenuNotify: function(value) {
      console.log(this.$options.name, "onChangedMenuNotify from", value);
      // demoを初期に
      this.disabledemo = true;
      this.checked = false;
      this.selected = null;
      this.stopTimer();
      this.resetTimer();
    },
    onDemoChanged: function( value ) {
      if(value) {
        if(this.selected) {
          let mnow = moment(new Date);
          this.fromnow = mnow.format('YYYY/MM/DD HH:mm:ss');
          // alert(event.target.tagName);
          // alert(this.selected);
          this.toend = mnow.add(this.selected, "minutes").format('YYYY/MM/DD HH:mm:ss');

          // 他のcomponent(Map.vue)に通知
          if(this.apptype == CONSTANTS.APPTYPE.RFTAG) {
            this.$eventHub.$emit('reset-demo-notify-rftag', this.selected, value, this.apptype);
          } else if(this.apptype == CONSTANTS.APPTYPE.CAMERA) {
            this.$eventHub.$emit('reset-demo-notify-camera', this.selected, value, this.apptype);
          }

          this.diffTime = this.selected * 60 * 1000;
          this.stopTimer();
          this.resetTimer();
          this.startTimer();
        } else {
          // this.$nextTick(() => {
          //   this.checked = false;
          // });
        }
      } else {
        this.stopTimer();
      }
    },
    onMinutesChanged: function( value ) {
      this.stopTimer();
      if(value == null) {
        this.disabledemo = true;
        this.checked = false;
        this.resetTimer();
        // 他のcomponent(Map.vue)に通知
        this.$eventHub.$emit('reset-demo-notify-rftag', null, this.checked, this.apptype.apptype);
        this.$eventHub.$emit('reset-demo-notify-camera', null, this.checked, this.apptype.apptype);

        // 自己コンポーネントをリロードする
        // this.$router.go({path: this.$router.currentRoute.path, force: true});
      } else {
        this.disabledemo = false;
        this.$nextTick(() => {
          this.onDemoChanged(this.checked);
        });
      }
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },

  	setStartTime(time){
    //performance.now()自体は前回のページから今回のページへと、遷移を開始した瞬間からの経過時間を算出する
      this.startTime = performance.now() - time;
    },
  	startTimer(){
    	if(this.flag){
      	return false;
      }
      const vm_data = this;
    	this.flag = true; // スタートしたらタイマーflagをtrueに
      this.setStartTime(vm_data.diffTime);
      (function progress(){
        vm_data.endTime = performance.now();
      	vm_data.diffTime = vm_data.endTime - vm_data.startTime;
     		[vm_data.second, vm_data.milisecond] = [Math.floor(vm_data.diffTime / 1000), Math.floor(vm_data.diffTime % 1000)];
        vm_data.animationId = window.requestAnimationFrame(progress);
      }());
    },
    stopTimer(){
      this.flag = false;   // ストップしたらタイマーflagをfalseに
			window.cancelAnimationFrame(this.animationId);
    },
    resetTimer(){
      if(this.flag){
      	return false;
      }
			this.startTime = this.diffTime = 0;
    },
  },

  computed:{
    checkHours(){
      return Math.floor(this.diffTime / 1000 / 60 / 60);
    },
    checkMinutes(){
      return Math.floor(this.diffTime / 1000 / 60) % 60;
    },
  	checkSeconds(){
      return Math.floor(this.diffTime / 1000) % 60;
    },
    checkMiliSeconds(){
      return Math.floor(this.diffTime % 1000);
    },
    apptype() {
      return this.$store.getters.apptype;
    },
  }	
};
</script>

