import App from "@/App.vue";
import BootstrapVue from "bootstrap-vue";
import router from "@/router";
import Vue from "vue";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-vue/dist/bootstrap-vue.min.css";





Vue.config.productionTip = false;
Vue.use(BootstrapVue);

// Vue.config.devtools = true;

import ElementUI from "element-ui"
import locale from "element-ui/lib/locale/lang/ja"
import "element-ui/lib/theme-chalk/index.css"
import "element-ui/lib/index.js"

Vue.use(ElementUI, {locale})

import axios from "axios"
Vue.prototype.$http = axios.create({
  // baseURL: "http://192.168.123.159:5000",
  timeout: 5000,
  headers: {'Content-Type': 'application/json'}
});

import { library } from '@fortawesome/fontawesome-svg-core'
import { faCoffee, faHistory, faMap, faChartLine, faCamera, faCameraRetro, faCogs, faSync, faTags, faShoppingCart, faImages } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faCoffee, faHistory, faMap, faChartLine, faCamera, faCameraRetro, faCogs, faSync, faTags, faShoppingCart, faImages)
Vue.component('font-awesome-icon', FontAwesomeIcon)


import EventHub from '@/EventHub'

Vue.use(EventHub)

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");
