<template>
  <div>
    <main id="mainContent" class="container-fluid">
      <div class="row">
        

        <div class="col-md-12 p-0 border-right sidebar">
            <!-- <CameraImage  v-on:GET_AJAX_COMPLETE="hAjaxCompleted" ref="chil" /> -->
            <HelloWorld  v-on:GET_AJAX_COMPLETE="hAjaxCompleted" ref="chil" />
        </div>



      </div>
    </main>
  </div>
</template>

<script>
import moment from 'moment';

moment.locale('ja');

import CONSTANTS from "@/constants";
// import CameraImage from "@/components/CameraImage";
import HelloWorld from "@/components/HelloWorld";

// import {DatePicker} from "element-ui";
import {AgGridVue} from "ag-grid-vue";

import axios from "axios";



export default {
  name: "List",
  serverCacheKey: false,

  components: {
    // DatePicker,
    AgGridVue,
    // CameraImage,
    HelloWorld
  },

  data() {
    return {
      count: 10,
      datetime: '',
      gridOptions: null,
      gridOptions2: null,
      gridApi: null,
      columnApi: null,
      defaultColDef: null,
      columnDefs: null,
      columnDefs2: null,
      intervalid1: null,
      intervalid2: null,
      items: [{hello: "Tag"}],
      amtOfData: null,
      crOfMac: null,
    }
  },

  created() {
    // clearInterval(this.intervalid1);
    // clearInterval(this.intervalid2);

    // // 上記コードの場合、最初の１回目は直ちに実行されません
    // let self = this;
    // this.intervalid1 = setInterval( ( function hoge(self) {
    //   self.get_ajax();
    //   return hoge;
    // }(this) ), CONSTANTS.TIMER.RELOAD * 1000, this );

    // if(!this.$eventHub._events['reset-demo-notify-camera']) {
    //   // イベント通知のハンドラを登録
    //   this.$eventHub.$on('reset-demo-notify-camera', this.onResetDemoNotify);
    // }

    // if(!this.$eventHub._events['changed-menu-notify-camera']) {
    //   // イベント通知のハンドラを登録
    //   this.$eventHub.$on('changed-menu-notify-camera', this.onChangedMenuNotify);
    // }
  },

  beforeDestroy() {
    // clearInterval(this.intervalid1);
    // this.intervalid1 = null;
    // clearInterval(this.intervalid2);
    // this.intervalid2 = null;
  },

  beforeMount() {
    // this.gridOptions = {};
    // this.gridOptions2 = {};
    // this.defaultColDef = {resizable: true};
    // this.columnDefs = [
    //   {headerName: 'ID', field: 'id', width: 50},
    //   {headerName: '日時', field: 'datetime', suppressSizeToFit: true}
    // ];
    // this.columnDefs2 = [
    //   {headerName: 'ID', field: 'id', width: 50},
    //   {headerName: '日時', field: 'datetime', suppressSizeToFit: true}
    // ];

    // // 初期なし状態
    // this.$store.dispatch('doUpdateTags', {tags: [], type: 0});
    // this.$store.dispatch('doUpdateTags', {tags: [], type: 1});
    // this.$store.dispatch('doUpdateImages', {images: []});
  },

  mounted() {
    // this.gridApi = this.gridOptions.api;
    // this.gridColumnApi = this.gridOptions.columnApi;

    // console.log(this.$router.currentRoute.path);
    // if(this.$router.currentRoute.path === '/'+CONSTANTS.APPTYPE.CAMERA_STR) {
    //   this.$store.dispatch('doUpdateApptype', CONSTANTS.APPTYPE.CAMERA);
    // }
  },

  methods: {
    onChangedMenuNotify: function(value) {
      console.log(this.$options.name, "onChangedMenuNotify from", value);

      clearInterval(this.intervalid1);
      clearInterval(this.intervalid2);

      // カレントコンポーネントをリロードする
      // this.$router.go({path: this.$router.currentRoute.path, force: true});
    },
    onResetDemoNotify: function(value, demoonoff, apptype) {
      console.log(this.$options.name, "onResetDemoNotify", value, demoonoff, apptype);
      if(apptype != CONSTANTS.APPTYPE.CAMERA ) return;

      // リセットボタンを押した処理
      clearInterval(this.intervalid1);
      clearInterval(this.intervalid2);

      if(demoonoff) {
        this.$store.dispatch('doUpdateTags', {tags: [], type: 0});
        this.$store.dispatch('doUpdateTags', {tags: [], type: 1});
        this.$store.dispatch('doUpdateImages', {images: []});
        this.parentCall(true);
      }

      // alert("onResetDemoNotify "+value);
      if(value == null) {
        // // カレントコンポーネントをリロードする
        // this.$router.go({path: this.$router.currentRoute.path, force: true});
        let self = this;
        this.intervalid1 = setInterval( ( function hoge(self) {
          self.get_ajax();
          return hoge;
        }(this) ), CONSTANTS.TIMER.RELOAD * 1000, this );

      } else {
        let mfrom = moment(new Date());
        let from = mfrom.format();
        let to = mfrom.add(value, 'm').format();

        this.$store.dispatch('doUpdateTags', {tags: [], type: 0});
        this.$store.dispatch('doUpdateTags', {tags: [], type: 1});
        this.$store.dispatch('doUpdateImages', {images: []});

        // console.log(1, this.count, value, from, to);

        // 今から今後画像を表示するため、リアルタイムで取得
        this.intervalid2 = setInterval( ( function hoge(self, value, from, to) {
          // console.log(2, self.count, value, from, to);
          self.get_ajax({'reset': true, 'minutes': value, 'endtime': 0, 'fromtime': from, 'totime': to});
          return hoge;
        }(this, value, from, to) ), CONSTANTS.TIMER.RESET * 1000, this, value, from, to );
      }
    },

    hAjaxCompleted: function (items) {
      console.log("GET_AJAX_COMPLETE", items);
    },

    get_ajax: function (params=null) {
      let self = this;

      // console.log('params', params);

      axios.get("/api/camera", {params: params}).then(res => {
        console.log(moment().format(), "get_ajax /api/camera");
        if(res.data.status == "success") {
          // console.log(res.data.contents);
          // self.items = res.data.contents;
          self.amtOfData = res.data.contents.amtOfData;
          self.crOfMac = res.data.contents.crOfMac;

          // save to store
          self.$store.dispatch('doUpdateTags', {tags: res.data.contents.ok, type: 1});
          self.$store.dispatch('doUpdateTags', {tags: res.data.contents.ng, type: 0});
          self.$store.dispatch('doUpdateImages', {images: res.data.contents.images});

          self.parentCall();
        }
      });
    },

    parentCall: function(reset=false) {
      if( this.$refs.chil ) {
        this.$refs.chil.disp_data(reset);
      }
    },

  },

  // computed: {
  //   okTags() {
  //     return this.$store.getters.okTags;
  //   },
  //   ngTags() {
  //     return this.$store.getters.ngTags;
  //   },
  //   images() {
  //     return this.$store.getters.images;
  //   },
  // },

};
</script>

<style scoped>
#maincontent { min-height: calc(100vh - 160px - 57px); }
#taglist, #tagerrorlist {width: 100%; height: 300px;}
</style>
