// index.js ... storeを作成する
import Vue from "vue";
import Vuex from "vuex";

import CONSTANTS from "@/constants";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    tags: null,
    okTags: [],
    ngTags: [],
    images: [],
    count: 0,
    apptype: CONSTANTS.APPTYPE.RFTAG,
    loading: false
  },
  getters: {
    loadingToString (state, _getters) {
      return state.loading ? 'loading...' : 'not loading now'
    },

    tags(state) {
      return state.tags;
    },

    okTags(state) {
      return state.okTags;
    },

    ngTags(state) {
      return state.ngTags;
    },

    images(state) {
      return state.images;
    },

    apptype(state) {
      return state.apptype;
    }
  },
  mutations: {
    increments (state) { state.count++ },
    decrements (state) { state.count-- },
    setCount(state, payload) { state.count = payload.count },
    showLoading (state) { state.loading = true },
    hideLoading (state) { state.loading = false },
    setTags(state, payload) {
      state.tags = payload.tags;
    },
    setOkTags(state, payload) {
      state.okTags = payload.tags;
    },
    setNgTags(state, payload) {
      state.ngTags = payload.tags;
    },
    setImages(state, payload) {
      state.images = payload.images;
    },
    setApptype(state, payload) { state.apptype = payload.apptype; }
  },
  actions: {
    increments (context) {
      context.commit('increments')
    },
    waitAndIncrements (context, payload) {
      setTimeout(() => { context.commit('increments') }, payload.decay)
    },
    fetchStationDetail(context, sid) {
        console.log(context, sid);
    },
    fetchStationPowers(context, args) {
        console.log(context, args)
    },
    doUpdateTags(context, args) {
      let tags = args.tags;
      if( args.type == 1) {
        context.commit('setOkTags', {tags});
      } else {
        context.commit('setNgTags', {tags});
      }
    },
    doUpdateImages(context, args) {
      let images = args.images;
      context.commit('setImages', {images});
    },
    doUpdateApptype(context, apptype) {
      context.commit('setApptype', {apptype});
    }
  }
});