<template>
  <div id=mapid>
  </div>
</template>

<script>
import  'leaflet/dist/leaflet.css'
import  L from 'leaflet'
require( 'leaflet.markercluster' )
import  'leaflet.markercluster/dist/MarkerCluster.css'
import  'leaflet.markercluster/dist/MarkerCluster.Default.css'

export default {
  name: "SpotMap",
  serverCacheKey: false,

  data() {
    return {
      msg: 'Welcome to here!',
      map: null,
      mcg: null,
    }
  },

  methods: {
    clearMap() {
      this.map.removeLayer(this.mcg);
    },
  },
  beforeDestroy() {
    this.clearMap();
  },

  mounted() {
      this.map = L.map( 'mapid', { center: L.latLng( 34.704552, 135.496629 ), zoom: 17 } ).addLayer(
          L.tileLayer( '/tiles/UMEDA/{z}/{x}/{y}.png' )
      )
      this.mcg = L.markerClusterGroup({
        spiderfyOnMaxZoom: true,
        showCoverageOnHover: false,
        zoomToBoundsOnClick: true
      });


      this.mcg.on('clusterclick', function(a) {
        console.log('Cluster Clicked:' + a);
      });
      this.mcg.on('click', function(a) {
        console.log('Marker Clicked:' + a);
      });
  },

};
</script>

<style scoped>
/* #mapid { height: 600px } */
#mapid { min-height: calc(100vh - 160px - 57px); }
</style>
